import { Component, OnInit, OnDestroy, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { ApiService, END_POINT } from '../../../core/services/api.service';
import { WordLimitPipe } from '../../pipe/word-limit.pipe';
import { LocalerService } from 'src/app/core/services/localer.service';
import { DeactivateDialogService } from 'src/app/core/sevices/deactivate-dialog.service';

@Component({
  selector: 'app-list-card',
  templateUrl: './list-card.component.html',
  styleUrls: ['./list-card.component.scss']
})
export class ListCardComponent implements OnInit, OnChanges {
  subscribe;
  id: any;
  objCartList = {user: '', favoriteUser: []};
  idFavorite: any; // STT ca user hin ti trong DS favorite.
  favoriteList: Array<any> = [];


  @Input() listProduct: any;

  listInfoProduct$: Array<any> =  [];

  constructor(
    private api: ApiService,
    private localer: LocalerService,
    private dialogService: DeactivateDialogService
  ) {  }

  ngOnInit() {
    this.api.getAssets(END_POINT.newsJson).subscribe(data => {
      data.man.forEach( item => {
        if (this.listProduct.find( i => i === item.id)) {
          this.listInfoProduct$.push(item);
        }
      });
      data.women.forEach( item => {
        if (this.listProduct.find( i => i === item.id)) {
          this.listInfoProduct$.push(item);
        }
      });
      data.child.forEach( item => {
        if (this.listProduct.find( i => i === item.id)) {
          this.listInfoProduct$.push(item);
        }
      });
      console.log(this.listInfoProduct$);
    });

  }

  ngOnChanges() {
    console.log('change data ' + this.listProduct);
  }

  addFavorite($event) {
    $event.target.classList.toggle('is_animating');
    this.idFavorite = -1;
    if (this.localer.getLocalStorage('favorite')) {
      this.favoriteList = JSON.parse(this.localer.getLocalStorage('favorite'));
    }
    if (this.favoriteList) {
      this.idFavorite = this.favoriteList.findIndex(item => item.user === this.localer.getSessionStorage('login'));
    }
    if (this.idFavorite === -1) {// if not exist
      this.objCartList.user = (this.localer.getSessionStorage('login'));
      this.objCartList.favoriteUser.push( Number($event.target.id.substr(10)) );
      this.favoriteList.push(this.objCartList);
      this.localer.saveLocalStorage('favorite', this.favoriteList);
    } else {
      this.objCartList = this.favoriteList[this.idFavorite];
      if (!this.objCartList.favoriteUser.includes( Number($event.target.id.substr(10)))) {
        this.favoriteList[this.idFavorite].favoriteUser.push(Number($event.target.id.substr(10)));
        this.localer.saveLocalStorage('favorite', this.favoriteList);
        $event.target.classList.toggle('favorite');
      } else {
        // nu product  tn ti trong danh sch yu thch th xo
        if (confirm('Do you want to dis favorite?')) {
          // tslint:disable-next-line:max-line-length
          this.favoriteList[this.idFavorite].favoriteUser = this.favoriteList[this.idFavorite].favoriteUser.filter( item => item !== Number($event.target.id.substr(10)));
          this.localer.saveLocalStorage('favorite', this.favoriteList);
          $event.target.classList.remove('favorite');
          window.location.reload();
        }
      }
    }
  }
}
